
import {
    FormItem,
} from '@/Types';
import { Options, Vue } from 'vue-class-component';

@Options({
    props: {
        value: Object,
    },
    emits: ['input', 'action'],
})
export default class FieldList extends Vue {
    private value!: FormItem;

    private newValue = '';

    get list() {
        let value = this.value.value;

        if (!Array.isArray(value)) {
            value = [value as string];
        }

        return value;
    }

    private keyboard(evt: KeyboardEvent) {
        const key = evt.key;
        const newValue = this.newValue;
        const value = this.list;

        switch (key) {
            case 'Enter':
                this.newValue = '';
                this.$emit('input', value.concat(newValue));
                break;
            case 'Escape':
                this.newValue = '';
                break;
        }
    }

    private remove(idx: number) {
        const list = this.list;
        const newList = [...list.slice(0, idx), ...list.slice(idx + 1)];
        this.$emit('input', newList);
    }
}
