
import {
    FormItem,
} from '@/Types';
import { Options, Vue } from 'vue-class-component';

import FieldInput from '@/components/fields/FieldInput.vue';
import FieldTextarea from '@/components/fields/FieldTextarea.vue';
import FieldText from '@/components/fields/FieldText.vue';
import FieldSelect from '@/components/fields/FieldSelect.vue';
import FieldList from '@/components/fields/FieldList.vue';
import FieldButton from '@/components/fields/FieldButton.vue';
import FieldImage from '@/components/fields/FieldImage.vue';

@Options({
    props: {
        value: Object,
    },
    emits: ['input', 'action'],
    components: {
        FieldInput,
        FieldTextarea,
        FieldText,
        FieldSelect,
        FieldList,
        FieldButton,
        FieldImage,
    },
})
export default class Field extends Vue {
    private value!: FormItem;

    private get componentKind() {
        const mapping = {
            text: 'FieldInput',
            number: 'FieldInput',
            boolean: 'FieldInput',
            range: 'FieldInput',
            longText: 'FieldTextarea',
            empty: 'FieldText',
            emptyLine: 'FieldText',
            rawText: 'FieldText',
            select: 'FieldSelect',
            list: 'FieldList',
            button: 'FieldButton',
            image: 'FieldImage',
        };

        const kind = mapping[this.value.type];
        return kind === undefined ? 'input' : kind;
    }

    private get labelDisplay() {
        const label = this.value.label;
        if (!label) {
            return '';
        }
        return `${label}:`;
    }

    private get unit(): string {
        const unit = this.value.unit;
        if (typeof unit !== 'string') {
            return '';
        }
        return unit;
    }
}
