
import {
    FormItem,
    FieldSelectOption,
} from '@/Types';
import { Options, Vue } from 'vue-class-component';


@Options({
    props: {
        value: Object,
    },
    emits: ['input', 'action'],
})
export default class FieldSelect extends Vue {
    private value!: FormItem;

    get options() {
        const options = (this.value.options || []) as FieldSelectOption[];
        const id = this.value.id;

        options.forEach((opt, idx) => {
            if (!opt.id) {
                opt.id = opt.value || `option-${id}-${idx}`;
            }
            if (opt.text === undefined) {
                opt.text = opt.value;
            }
        });

        return options;
    }

    private change(evt: Event) {
        const element = evt.currentTarget as HTMLSelectElement;
        const value = element.value;

        this.$emit('input', value);
    }
}
